.glide {
  width: 100%;
  box-sizing: border-box;

  &--gallery {
    overflow: hidden;
  }
}

.glide * {
  box-sizing: inherit
}

.glide__track {
  position: relative;

  &--hideleft {
    &::before {
      z-index: 2;
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: -500px;
      right: 100%;
      background: $white;
      content: "";
    }
  }
}

.glide__slides {
  position: relative;
  width: 100%;
  list-style: none;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  touch-action: pan-Y;
  padding: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  will-change: transform
}

.glide__slides--dragging {
  user-select: none
}

.glide__slide {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  white-space: normal;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent
}

.glide__slide a {
  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none
}

.glide__arrows {
  -webkit-touch-callout: none;
  user-select: none;
  position: absolute;
  right: 0;
  bottom: 100%;
  z-index: 5;
  height: 40px;
}


.glide__arrow {
  width: 40px;
  height: 25px;
  background: none;
  border: 0;
  padding: 0;
  margin: 0 0 0 8px;
  color: $primary;

  &:hover,
  &:focus {
    outline: none;
    color: $secondary;
    cursor: pointer;
  }

  svg {
    width: 25px;
    height: 25px;
    fill: currentColor;
    transition: all 0.3s linear;
  }
}

.glide__bullets {
  -webkit-touch-callout: none;
  user-select: none
}

.glide--rtl {
  direction: rtl
}
