.footer {
  margin-top: 24px;
  background-color: $gray-medium;
}

.footer__menu {
  margin-left: auto;
  padding-top: 24px;
}

.footer__inner {
  display: flex;
}

.footer__block {
  padding: 32px 0 24px;
  width: 50%;
  text-align: center;

  &:first-child {
    text-align: left;
    margin-right: auto;
  }

  &:last-child {
    text-align: right;
    margin-left: auto;
  }
}

.footer__timetable {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-start;
}

.footer__weekday {
  list-style: none;

  &+& {
    margin-left: 24px;
  }
}

.footer__title {
  margin-bottom: 16px;
}

.footer__text {
  max-width: 400px;
  margin: 0 0 0 auto;
  padding: 0px 0 8px;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.footer__social {
  display: flex;
  justify-content: flex-end;
}

.footer__social-link {
  margin-top: 8px;
  margin-left: 16px;
  transition: all 0.15s linear;
  color: $primary;

  &:hover,
  &:focus {
    color: $secondary;
  }

  svg {
    fill: currentColor;
  }
}
