.advantages__wrp {
  padding-top: 24px;
  padding-bottom: 24px;
}

.advantages__list {
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0 -16px;
  list-style: none;
}

.advantages__item {
  width: 300px;
  background-color: rgba($secondary, 0.5);
  margin: 0 16px;
  padding: 32px;
  text-align: center;

  h3 {
    text-transform: uppercase;
    font-size: 16px;
    margin-bottom: 12px;
    font-weight: 300;
    max-width: 195px;
    margin-left: auto;
    margin-right: auto;
  }

  p {
    font-size: 16px;
    margin: 0;
  }
}

.advantages__icon svg {
  width: 55px;
  height: 55px;
  margin-bottom: 20px;
}
