@mixin underline {
  position: relative;
  padding-bottom: 4px;
  padding-left: 4px;
  padding-right: 4px;
  margin-bottom: 4px;

  transition: all 0.15s linear;

  &::before,
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    height: 1px;
    background-color: $black;
    transition: all 0.15s linear;
  }

  &::before {
    left: 50%;
    right: 50%;
  }

  &::after {
    left: 50%;
    right: 50%;
  }

  &:hover::before {
    left: 0;
    right: 50%;
  }

  &:hover::after {
    left: 50%;
    right: 0;
  }
}
