.address {
  padding-top: 12px;
  padding-bottom: 32px;
  margin-bottom: 32px;
}

.address__heading {
  color: $primary;
}

.address__inner {
  display: flex;
  justify-content: space-between;
  padding-top: 32px;
  padding-bottom: 32px;
}

.address__decor {
  width: calc(100% - 32px);
  height: 400px;
  transform: translate(-24px, 16px);
  box-shadow: 24px -24px 0 0 $secondary, -4px 4px 10px $gray-light;
  overflow: hidden;

  img {
    width: 100%;
    height: auto;
  }
}

.address__content {
  width: calc(33.3333% + 32px);
  flex-shrink: 0;
}

.address__text {
  text-align: right;
  padding-right: 32px;
  padding-bottom: 16px;


  p {
    font-size: 20px;
    margin: 0;
    padding: 0;
  }

  a {
    font-size: 20px;
    display: inline-block;
    margin: 0;
    padding: 0;
    @include underline;
  }
}

.address__map {
  flex-grow: 1;
  position: relative;
  min-height: 500px;

  &::before {
    content: "";
    position: absolute;
    bottom: -53px;
    right: -500px;
    left: -120px;
    height: 500px;
    background-color: $gray-medium;
    z-index: -1;
  }

  // box-shadow: 24px -24px 0 0 #D9D0C7, -4px 4px 10px #F0F0F0;

  div#map {
    width: 100%;
    height: 100%;
    background-color: $secondary;
  }
}
