.price__wrp {
  padding-top: 24px;
  padding-bottom: 24px;
}

.price {
  padding-bottom: 24px;
}

.price__heading {
  color: $primary;
  padding-bottom: 8px;

  span {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      left: -300px;
      right: -8px;
      top: 12px;
      bottom: -4px;
      background-color: $secondary;
      z-index: -1;
    }
  }
}

.price__comment {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
  font-style: italic;
  padding-bottom: 24px;

  p {
    margin: 0;
    padding: 0;
  }
}

.price__block {

  &:not(:last-of-type) {
    padding-bottom: 24px;
  }
}

.price__list {
  padding: 0;
  margin: 0;
}

.price__item {
  list-style: none;
  border-bottom: 1px dotted $primary;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: $primary;
  transition: all 0.3s linear;

  &:hover {
    color: $black;
    border-bottom-color: $black;
  }
}

.price__service {
  font-size: 16px;
  margin: 8px 0 -8px;
  padding: 2px 2px 2px 0;
  text-transform: lowercase;
  background-color: $white;
  color: inherit;
}

.price__amount {
  font-size: 16px;
  margin: 8px 0 -8px;
  padding: 2px 0 2px 4px;
  font-weight: 700;
  background-color: $white;
  color: inherit;
}
