.header {
  margin-bottom: 24px;
}

.header__inner {
  display: flex;
  align-items: flex-start;
  padding-top: 16px;
}

.header__content {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  margin-left: auto;
}

.header__contacts {
  width: 50%;
}

.header__info {
  margin-left: 24px;
}

.header__text {
  display: block;
  margin: 0;
  padding: 8px 0;
  font-size: 16px;
  color: $black;
  text-decoration: none;

  &+& {
    padding-top: 0;
  }

  &--phone {
    @include underline;
  }

  &--address {
    display: flex;
    align-items: center;
  }

  svg {
    margin-right: 8px;
  }
}

.header__menu {
  margin-top: 16px;
  margin-left: auto;
}
