.intro__wrp {
  overflow-x: hidden;
  padding-top: 24px;
  padding-bottom: 24px;
}

.intro {
  position: relative;

  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.intro__title {
  margin: 0;
  padding: 16px 0 0 0;
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  font-size: 16px;

  &+& {
    padding-top: 8px;
  }
}

.intro__caption {
  margin: 0;
  font-size: 16px;
  line-height: 22px;
}

.intro__inner {
  position: relative;
  display: flex;
  padding: 0 0 95px 0;
  margin: 0;

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: -500px;
    left: 630px;
    height: 500px;
    background-color: $gray-medium;
    z-index: -1;
  }
}


.intro__half {
  width: 440px;
  padding-left: 16px;

  &--right {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    align-content: flex-end;
    flex-wrap: wrap;
  }
}

.intro__pic {
  overflow: hidden;

  &--main {
    width: 420px;
    height: 450px;
  }

  &--topleft {
    padding-right: 16px;
    width: 220px;
    height: 300px;
  }

  &--topright {
    width: 200px;
    height: 200px;
  }

  &--bottom {
    width: 330px;
    height: 250px;
    padding-top: 16px;
  }

  img,
  video {
    width: 100%;
    height: auto;
    min-height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: center;
    object-position: center;
  }
}
