body {
  margin: 0;
  color: $black;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 1200px;
  // background-image: url('./img/Untitled-1.jpg');
  // background-position: top center;
  // background-repeat: no-repeat;
  font-family: $main-font;

  * {
    box-sizing: border-box;

    &::selection {
      background: $secondary;
      /* Safari */
    }

    &::-moz-selection {
      background: $secondary;
      /* Firefox */
    }
  }
}

main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 120px;
}


h1,
h2,
h3,
h4,
h5 {
  font-family: $title-font;
  font-weight: 700;
  margin-top: 0;
}

img {
  width: 100%;
  height: auto;
}

p {
  font-size: 16px;
  margin: 0;
  padding: 8px 0;
}

a {
  font-size: 16px;
  margin: 0;
  padding: 8px 0;
  text-decoration: none;
  color: inherit;
}
