.menu {

  @media #{$breakpointTablet} {
    display: none;
  }
}

.menu__list {
  display: flex;
  margin: 0;
  padding: 0;
}

.menu__item {
  list-style: none;
  padding-left: 16px;
}

.menu__link {
  color: currentColor;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;

  &:not(.menu__link--accent) {
    @include underline;
  }

  &--accent {
    padding: 4px;
    border: 1px solid transparent;
    transition: all 0.15s linear;
    background-color: $accent;

    &:hover,
    &:focus {
      background-color: $secondary;
      border: 1px solid currentColor;
    }
  }
}
