.offers__wrp {
  padding-top: 24px;
  padding-bottom: 24px;
}

.offers {
  position: relative;
  padding-top: 32px;
  padding-bottom: 32px;

  &::before {
    content: '';
    position: absolute;
    left: -300px;
    right: 340px;
    top: 0;
    bottom: 0;
    background-color: $gray-medium;
    z-index: -1;
    transition: all 0.3s linear;
  }
}

.offers__wrp {
  overflow-x: hidden;
}

.offers__list {
  width: 100%;
  list-style: none;
  margin: 32px 0 0;
  padding: 0;
}

.offers__item {
  position: relative;
  max-width: 455px;
  min-height: 270px;
  flex-shrink: 0;
  display: flex;
  background-color: $white;
  margin: 12px 0 12px;
  transition: all 0.3s linear;

  &:nth-child(2n-1) {
    box-shadow: -16px -16px 0 0 $secondary, 4px 4px 10px $gray-light;
    ;
  }

  &:nth-child(2n) {
    box-shadow: 16px 16px 0 0 $secondary, -4px -4px 10px $gray-light;
  }

  &:hover {
    &:nth-child(2n-1) {
      box-shadow: -20px -20px 0 0 $secondary, 4px 4px 10px $gray-light;
    }

    &:nth-child(2n) {
      box-shadow: 20px 20px 0 0 $secondary, -4px -4px 10px $gray-light;
    }

    .offers__img::before {
      background-color: rgba($gray-dark, 0);
    }

    img {
      transform: scale(1.05);
    }
  }
}

.offers__img {
  width: 227px;
  height: 305px;
  position: relative;
  overflow: hidden;


  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba($gray-dark, 0.3);
    transition: all 0.3s linear;
    z-index: 1;
  }

  img {
    height: 100%;
    object-fit: cover;
    object-position: center;
    transition: all 0.3s linear;
  }
}

.offers__text {
  width: 50%;
  padding: 48px 24px 24px;
  // background-color: rgba($gray-light, 0.5);
}

.offers__caption {
  font-family: $title-font;
  font-weight: 300;
  font-size: 16px;
  text-transform: uppercase;
  margin: 0 0 16px;
}

.offers__price {
  margin: 0;
}

.offers__description {
  font-size: 16px;
  margin: 0;
  padding: 8px 0;
}
