.brands__wrp {
  padding-top: 24px;
  padding-bottom: 24px;
}

.brands__text {
  max-width: 66%;
}

.brands__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
  margin-left: -16px;
  margin-right: -16px;
}

.brand__item {
  list-style: none;
  margin: 0;
  padding: 0 16px;
  width: 25%;
  height: 112px;
  display: flex;
  align-items: center;
  // filter: grayscale(1);
  overflow: hidden;

  &:hover img {
    transform: scale(1.05);
  }

  img {
    object-fit: contain;
    object-position: center;
    transition: all 0.3s linear;
  }
}
